.switch
  position: relative
  display: inline-block
  width: 30px
  height: 17px
  margin-right: 8px

  input
    opacity: 0
    width: 0
    height: 0

.slider
  position: absolute
  cursor: pointer
  top: 0
  left: 0
  right: 0
  bottom: 0
  background-color: #ccc
  -webkit-transition: .4s
  transition: .4s
  border-radius: 8px

  &:before
    position: absolute
    content: ""
    height: 13px
    width: 13px
    left: 3px
    bottom: 2px
    background-color: white
    -webkit-transition: .4s
    transition: .4s
    border-radius: 50%

input:checked + .slider
  background-color: #2196F3

input:focus + .slider
  box-shadow: 0 0 1px #2196F3

input:checked + .slider:before
  -webkit-transform: translateX(10px)
  -ms-transform: translateX(10px)
  transform: translateX(10px)

.theme-switcher-text
  vertical-align: middle